body {
 margin: 0px;
 padding: 0px;
 background-color: rgb(248, 248, 248);
}
.appWrapper {
    display: grid;
    width: 100%;
    margin: 0 auto;
    
    grid-template-areas: 
    "header header"
    "nav content";
    grid-template-rows: 70px 1fr;
    grid-template-columns: 1.5fr 10fr;
}